import api from '@/api/index.js'

const config = {
    namespaced: true,
    actions: {
        configBizExecutor ({commit}, data) {
            return api.configBizExecutor(data).then(response => response.data)
        },
        loadBizConfig () {
            return api.loadBizConfig().then(
                response => response.data
            )
        }
    }
}

export default config